.input_container {
    display: flex;
    align-items: center;
    position: relative;
    gap: 20px;
    width: 100%;
}

.input_style {
    width: 100%;
    height: 30px;
    outline: none;
    box-sizing: border-box;
    border: 0;
    border-bottom: 1.5px solid #9d9d9d;
    background-color: transparent;
}

.input_message_error {
    position: absolute;
    left: 10px;
    top: -7px;
    color: red;
    font-size: 0.7em;
    background: linear-gradient(to bottom, #eff7ff, #fff);
    border-left: 1px solid red;
    border-right: 1px solid red;
    border-radius: 2px;
    padding: 0 5px;
}

.input_error {
    border: 1px solid red;
}