.todo_List {
    display: flex;
    flex-direction: column;
    height: fit-content;
    gap: 10px;
    padding: 20px;
    border-radius: 10px;
    background: linear-gradient(to bottom, rgba(136, 136, 136, 0.05), #d6e4f3);
    box-shadow: 15px 15px 20px rgba(0,0,0,0.1), -15px -15px 20px #fff;
    position: relative;
    overflow: hidden;
    width: 300px;
    box-sizing: border-box;
}

.filter_container {
    display: flex;
    gap: 15px;
}

.scale {
    transform: scale(0);
}

.task_list {
    display: flex;
    flex-direction: column;
    list-style: none;
    padding: 0;
    gap: 10px;
}


